	module.exports = {"data":{"markdownRemark":{"html":"<p>Gatsby uses a React component to server render the and other parts of the HTML outside of the core Gatsby application.</p>\n<p>Most sites should use the default html.js shipped with Gatsby. But if you need to customize your site’s html.js, simply copy the default one into your source tree by running:</p>\n<pre><code class=\"language-js\">import React from 'react';\n\nexport default function Template({\n  data // this prop will be injected by the GraphQL query below.\n}) {\n  const { markdownRemark } = data; // data.markdownRemark holds our post data\n  const { frontmatter, html } = markdownRemark;\n  console.log(frontmatter);\n  return (\n    &#x3C;div className=\"blog-post-container\">\n      &#x3C;div className=\"blog-post\">\n        &#x3C;p>{frontmatter.title}&#x3C;/p>\n        &#x3C;p>{frontmatter.date}&#x3C;/p>\n        &#x3C;div\n          className=\"blog-post-content\"\n          dangerouslySetInnerHTML={{ __html: html }}\n        />\n      &#x3C;/div>\n    &#x3C;/div>\n  );\n}\n</code></pre>\n<p>what about css?</p>\n<pre><code class=\"language-css\">/* from prism */\ncode[class*='language-'],\npre[class*='language-'] {\n  color: #f8f8f2;\n  background: none;\n  text-shadow: 0 1px rgba(0, 0, 0, 0.3);\n  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;\n  text-align: left;\n  white-space: pre;\n  word-spacing: normal;\n  word-break: normal;\n  word-wrap: normal;\n  line-height: 1.5;\n\n  -moz-tab-size: 4;\n  -o-tab-size: 4;\n  tab-size: 4;\n\n  -webkit-hyphens: none;\n  -moz-hyphens: none;\n  -ms-hyphens: none;\n  hyphens: none;\n}\n</code></pre>","frontmatter":{"date":"December 16, 2015","path":"/blog/test","title":"test"}}},"pathContext":{}}

